@import "vars";

.card {
    font-family: "open-sans", sans-serif;
    background: color-mix(in srgb, $accent, transparent 80%);
    border-radius: 5px;
    padding: 10px;
    width: fit-content;

    h1 {
        font-size: 32px;
        margin: 0;
    }
    
    h3 {
        font-size: 15px;
        opacity: 50%;
        margin: 0 0 5px;
    }
    
    p {
        margin: 0;
        font-size: 15px;
        max-width: 700px;
    }
}

.card-tags {
    margin-top: 10px;

    .card-tag {
        width: fit-content;
        border-radius: 10px;

        margin-bottom: 7px;
        margin-right: 7px;

        p {
            padding: 3px 6px;
            font-size: 13px;
            font-weight: 600;
            color: $bg;
        }
    }

    .card-tag-section {
        display: flex;
        flex-wrap: wrap;
        max-width: 450px;
    }
}

.timeline-arrow {
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid $p-text;
    border-radius: 1px;
    justify-self: right;
    margin-right: 10px;
}

.card-header {
    display: grid;
    grid-template-columns: 20px 1fr;
    align-items: center;
    transform: translateX(-20px);
}

.timeline-diamond {
    width: 22px;
    height: 22px;
    border: 6px solid $accent;
    background-color: $bg;
    border-radius: 2px;
    transform: translateX(-20px) translateY(15px) rotate(45deg);
}

.timeline-card-container {
    display: grid;
    grid-template-columns: 50px 1fr;

    .card {
        margin-bottom: 50px;
    }

    &:nth-child(2) {
        margin-top: 40px;
    }

    &:last-child {
        margin-bottom: 0px;
    }
}